<template>
  <div id="index" ref="appRef">
    <div class="bg">
      <!-- <dv-loading v-if="loading">Loading...</dv-loading> -->
      <div class="host-body">
        <video src="@/assets/headbg.mp4" class="vdis" autoplay loop="true" muted></video>
        <div class="d-flex jc-center"></div>
      </div>
      <div class="tabels" id="tScroll">
        <ul class="list head">
          <li class="fsd">
            <span>代码</span>
            <span class="fz">名称</span>
            <span class="nus">最新</span>
            <span class="nuz">涨跌额</span>
            <span class="nuf">涨跌幅</span>
            <span>昨收</span>
            <span>今天</span>
            <span>最高</span>
            <span>最低</span>
            <span style="width: 200px">成交量</span>
            <span>成交额</span>
            <span>市值</span>
            <span>换手</span>
          </li>
        </ul>
        <vue-seamless-scroll
          :data="dataChartulary"
          :class-option="optionSetting"
          class="seamless-warp"
          v-if="dataChartulary.length !== 0"
        >
          <ul class="list">
            <li v-for="(item, id) in dataChartulary" :key="id">
              <span>{{ item.stockCode }}</span>
              <span class="fz">{{ item.name }}</span>
              <!--
              <span
                style="color:#2BDCAE;font-weight: 600;"
                v-if="item.changeAmount >=-2.12+'%'"
              >{{item.priceNow}}</span>
              <span
                class="nus"
                :class="{'is-bule': parseFloat(item.priceNow) < 0 ? true: false }"
                v-else
              >{{item.priceNow }}</span>
              -->              
              <span
                class="nus"
                :class="{'is-bule': parseFloat(item.changeAmount) < 0 ? true: false }"               
              >{{item.priceNow }}</span>

              <span
                style="color:#2BDCAE;"
                v-if="item.changeAmount >=-2.12+'%'"
              >{{item.changeAmount }}</span>
              <span
                class="nuz"
                :class="{'is-bule': parseFloat(item.changeAmount) < 0 ? true: false }"
                v-else
              >{{item.changeAmount }}</span>

              <span style="color:#2BDCAE;" v-if="item.changeAmount >=-2.12+'%'">{{item.priceLimit}}</span>
              <span
                class="nuf"
                :class="{'is-bule': parseFloat(item.priceLimit) < 0 ? true: false }"
                v-else
              >{{item.priceLimit}}</span>

              <span>{{ item.preClose }}</span>
              <span>{{ item.open }}</span>
              <span>{{ item.high }}</span>
              <span>{{ item.low }}</span>
              <span style="width: 200px">{{ item.volume }}</span>
              <span>{{ item.amount }}</span>
              <span>{{ item.capitalization }}</span>
              <span>{{ item.turnoverRatio }}</span>
            </li>
          </ul>
        </vue-seamless-scroll>
        <div class="tabelsd" v-else>
          <ul class="list head">
            <li class="trsf">
              <dv-loading class="dvload">Loading...</dv-loading>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import drawMixin from "../utils/drawMixin";
import { formatTime } from "../utils/index.js";
export default {
  mixins: [drawMixin],
  data() {
    return {
      projectDesList: [
        {
          code: 300326,
          names: "上海",
          latents: "112.12"
        },
        {
          code: 300326,
          names: "上海",
          latents: "112.12"
        },
        {
          code: 300326,
          names: "上海",
          latents: "112.12"
        },
        {
          code: 300326,
          names: "上海",
          latents: "112.12"
        },
        {
          code: 300326,
          names: "上海",
          latents: "112.12"
        },
        {
          code: 300326,
          names: "上海",
          latents: "112.12"
        },
        {
          code: 300326,
          names: "上海",
          latents: "112.12"
        }
      ],
      dataChartulary: [],
      page: "1",
      count: "11",
      total: "",
      baseUrl: "http://jiangxi.originsystem.cn",
      timing: null,
      loading: true,
      dateDay: null,
      dateYear: null,
      dateWeek: null,
      weekday: ["周日", "周一", "周二", "周三", "周四", "周五", "周六"]
    };
  },
  computed: {
    optionSetting() {
      return {
        step: 0.45,
        limitMoveNum: 2,
        hoverStop: true,
        direction: 1,
        openWatch: true,
        singleHeight: 0,
        singleWidth: 0,
        waitTime: 1000
      };
    }
  },

  created() {

    console.log("created() ... ")

    let _THIS = this;
    _THIS.$nextTick(() => {
      _THIS.tableData();
    });
  },

  mounted() {
    console.log("mounted() ... ")
    this.timeFn();
    this.cancelLoading();
  },

  beforeDestroy() {
    clearInterval(this.timing);
  },

  methods: {
    timeFn() {
      this.timing = setInterval(() => {
        this.dateDay = formatTime(new Date(), "HH: mm: ss");
        this.dateYear = formatTime(new Date(), "yyyy-MM-dd");
        this.dateWeek = this.weekday[new Date().getDay()];
      }, 1000);
    },
    cancelLoading() {
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },

    tableData() {
      console.log("tableData() ... ")
      let _TH = this;
      // _TH.axios.get(_TH.baseUrl+`/web/zhangjianggupiao/public/index/admin/getStockInfo?page=${_TH.page}&count=${_TH.count}`).then((response) => {
      _TH.axios
        .get(
          _TH.baseUrl + "/web/zhangjianggupiao/public/index/admin/getStockInfo"
        )
        .then(response => {
          let resp = response.data.data;
          _TH.total = resp.total;

          var other = _TH.total % _TH.count == 0 ? 0 : 1;
          console.log("获取总共显示的页数", other);

          var allPage = parseInt(_TH.total / _TH.count) + other;

          console.log("获取总共的页数", allPage); //4

          console.log("获取表格总数", resp.total); //43
          _TH.dataChartulary = resp.result;
        });
    }
  }
};
</script>

<style lang="scss" scope>
.seamless-warp {
  height: 685px;
  overflow: hidden;
}
</style>
<style lang="scss">
@import "../assets/scss/index.scss";
.el-table td.el-table__cell {
  background-color: rgba(0, 78, 125, 0.9) !important;
  border-top: 2px solid #fff;
  color: #fff;
}

// 去掉表格单元格边框
.customer-table th {
  border: none;
}
.customer-table td,
.customer-table th.is-leaf {
  border: none;
}
// 表格最外边框
.el-table--border,
.el-table--group {
  border: none;
}

.customer-table::before {
  width: 0;
}
.customer-table .el-table__fixed-right::before,
.el-table__fixed::before {
  width: 0;
}
</style>